import * as React from 'react';
import Engine from './Engine';
import {Button} from 'react-bootstrap'

export const UPGRADE_NAMES: string[][] = [
  ['Villager Attraction','Villager Expansion','Power Over Faith','Power Accumulation'],//villager +,
  ['Minimum Happiness','Maximum Happiness','Faith Over Commerce','Faith Accumulation'],//min happiness+, max happiness+
  ['Currency Aquiring','Investing','Commerce Over Knowledge','Commerce Accumulation'],//
  ['Trait Identification','Teacher Training','Knowledge Over Power','Knowledge Accumulation']//vill cost -, vill cost for teacher -
]

export interface ProgressProps {
    engine: Engine;
    which: number
}

export default class Upgrade extends React.Component<ProgressProps, {}> {

  attract=()=>{
    this.props.engine.state.population += 1;
    this.props.engine.state.totalPop += 1;
    this.props.engine.notify();
  }

  do(which2: number){
    this.props.engine.upgrade(this.props.which, which2);
  }

  render() {
    
    let fn = [
      this.do.bind(this, [0]),
      this.do.bind(this, [1]),
      this.do.bind(this, [2]),
      this.do.bind(this, [3]),
    ]
    let engine = this.props.engine
    let which = this.props.which;
    //let state = this.props.engine.state;
    let upgs = UPGRADE_NAMES[this.props.which].slice(0,2)
    let btns = upgs.map(function(upg, index){
      return <Button onClick={fn[index]} key={index}>{UPGRADE_NAMES[which][index]}-{engine.state.upgrades[which][index]}</Button>
    })
    //console.log(btns);
    
    //let label = `Attract Villager (${state.population}/${state.totalPop})`;
    return (
      <div>
        {`Upgrade Cost: ${this.props.engine.upgradeCost(which)}`}<br/>
        {`Prestige (now|new): (${engine.state.upgradeResets[which]}|${engine.getPrestige()[which]})`}
        {btns}
      </div>
    );
  }
}



