import * as React from 'react';
import Engine from './Engine';
import {ProgressBar, Button} from 'react-bootstrap'

export interface ProgressProps {
    engine: Engine;
}

interface PState {
  completed: number;
}

export default class Happiness extends React.Component<ProgressProps, PState> {

  constructor(props: ProgressProps) {
    super(props);

    this.state = {
      completed: 0,
    };
  }

  timer: any;

  componentDidMount() {
    this.timer = setTimeout(() => this.progress(), 1000);
  }

  componentWillUnmount() {
    clearTimeout(this.timer);
  }

  progress() {
    let curHap = this.props.engine.state.happiness;
    let minHap = this.props.engine.state.upgrades[1][0] * 25;
    let maxHap = (this.props.engine.state.upgrades[1][1] * 10) + 100;
    if (curHap > maxHap) {
      this.props.engine.state.happiness = maxHap;
    } else if (curHap <= minHap) {
      this.props.engine.state.happiness = minHap;      
    } else {
      const diff = Math.random() * 1;
      this.props.engine.state.happiness -= diff;      
    }
    this.props.engine.notify();
    this.timer = setTimeout(() => this.progress(), 1000);
  }

  appease=()=>{
    this.props.engine.state.happiness += 10;
    this.props.engine.notify();
  }

  render() {

    let maxHap = (this.props.engine.state.upgrades[1][1] * 10) + 100;
    let calc = this.props.engine.state.happiness/maxHap

    return (
      <div>
        <Button onClick={this.appease}  >{`Appease Villagers`}</Button><br/>
        {`Happiness: ${this.props.engine.state.happiness.toString().slice(0,5)} : ${maxHap}`}<br/>
        {`${(calc*100).toFixed(2)}%`}<br/>
        <ProgressBar now={calc*100}/>
      </div>
    );
  }
}



