import * as React from 'react';
import Engine from './Engine';
import {Button} from 'react-bootstrap'
import {NAMES, STYLES} from './App'


export interface ProgressProps {
    engine: Engine;
}

interface PState {
  completed: number;
}

export default class Populaion extends React.Component<ProgressProps, PState> {

  constructor(props: ProgressProps) {
    super(props);

    this.state = {
      completed: 0,
    };
  }

  attract=()=>{
    this.props.engine.attract();
  }

  do=(which: number)=>{
    this.props.engine.levelUp(which);
  }

  render() {
    let state = this.props.engine.state;
    let label = `Attract Villager`;
    
    let fn = [
      this.do.bind(this, [0]),
      this.do.bind(this, [1]),
      this.do.bind(this, [2]),
      this.do.bind(this, [3]),
    ]

    let cost1 = this.props.engine.cost(1)
    let cost2 = this.props.engine.cost(3)
    let levelLabel = `Find a specialist amongst ${cost1 + " (" + cost2 + " for Teachers) "} villagers`;
    let btns = NAMES.slice(4,8).map(function(name, index){
      return <Button onClick={fn[index]} bsStyle={STYLES[index]} key={index}>Hire a {name}</Button>
    })
    return (
      <div>
        <Button onClick={this.attract}>{label}</Button><br/>
        {`Population: ${state.population}`}<br/>
        {levelLabel}<br/>
        {btns}
      </div>
    );
  }
}



